    async getSelection(Array: string, name: string) {
        let {data} = await api.getSelection(name);
        this[`${Array}`] = data;
    }

    async getApportionrecordDetail() {
        let {data} = await api.getApportionrecordDetail(this.resultId);
        this.detailObj = data;
    }
    factCoast:string | number = '';
