    async getResultList() {
        this.sharedFact();
        this.loading = true
        try {
           /* let {data: {content, totalElements}} = await api.getResultList({
                pageNum: this.pages.pageNum,
                pageSize: this.pages.pageSize,
                sharedFeeId: this.resultId,
                roomName: this.keyword,
                sharedFeeStatus: this.sharedFeeStatus,
            });*/
            let {data: {content, totalElements}} = this.thisData;
            this.resultList = content;
            this.pages.totalElements = totalElements;
            this.loading = false
        }catch (e) {
            this.loading = false
        }
    }

    getPage(pageNum?: number) {
        if (pageNum) {
            this.pages.pageNum = pageNum;
        } else {
            this.pages.pageNum = 1;
        }
        this.getResultList();
    }

    reset() {
        this.keyword = '';
        this.sharedFeeStatus = '';
        this.getPage();
    }

    backClose() {
        this.$emit(`close`)
    }

    tableId: string = ''

    editMoney(money, id) {

        this.money = money
        this.tableId = id
    }
    async resetResult() {
        if (this.subFlag) {
            this.subFlag = false;
            try {
                await api.resetResult(this.tableId, this.money)
                this.tableId = ''
                this.$message.success(`修改成功！`)
                this.reset();
            } catch (e) {
                this.$message.error(`修改失败！`)
            }
            this.subFlag = true;
        }


    }

    closeDialog(){
        let refs:any = this.$refs;
        refs.payDate.handleClear();
        this.dialogVisible = false;
    }
    setPayDate(val){
        this.payDate = val;
    }


    async confimBill() {
        if (this.subFlag) {
            this.subFlag = false;
            try {
                await api.createAccount(this.resultId,this.payDate);
                this.dialogVisible=false;
                this.hintDialog = true;
            } catch (e) {
                this.$message.error(`账单生成失败！`)
            }
            this.subFlag = true;
        }

    }

    recalculate() {
        api.judgeShared(this.resultId).then(res=>{
            if(res.data.result === 0){
                this.sharedCalc()
            }
            else{
                this.$message.error(`该项费用已有用户完成缴费，无法重新计算。`)
            }
        })
    }
    async sharedCalc(){
        if (this.subFlag) {
            this.subFlag = false;
            try {
                await api.sharedCalc(this.resultId,true);
                this.$message.success(`计算成功！`)
                this.reset();
            } catch (e) {
                this.$message.error(`计算失败！`)
            }
            this.subFlag = true;
        }

    }
